module.exports = "<p>Nothing much has changed. We still have a <em>lot</em> of work to do and the feedback received does add to the list of things to get right. However, it does mean that we could probably start sharing the links to a slightly larger group, making the circle a tiny bit bigger. The feedback loop is ever important.</p>\n<p>I addition, it does pave the way for <em>&quot;o, hell, people could actually see this&quot;</em>, so does put on some extra pressure. I guess the age-old rule of <em>&quot;if you are 100% happy with your product, you launched too late&quot;</em> applies here. Although, this is not quite a launch.</p>\n";

