module.exports = "<p><time>26 June 2017</time></p>\n<h2 id=\"soft-launching\">Soft launching</h2>\n<p>Well, at some point you need to take the training wheel off. And that day has come, the training wheels has just been taken off. Nothing much have changed, we have not throw open the doors and invited external use from anywhere, rather our invited alpha users now will just be able to find the wallet by not having to go to a &quot;hidden url&quot;.</p>\n<p>So what does this mean? So where to now? What is next?</p>\n";

